<template>
  <div>
      <!-- navbar -->
      <header>
        <div class="header">购物车</div>
      </header>
      <!-- 购物车主体页面 -->
    <div class="cart" v-if="getTotal > 0 && getToken">
      <!-- 购物车开头 -->
      <div class="cart-title">
        <span class="all"
          >共<i>{{ getTotal }}</i
          >件商品</span
        >
        <span class="edit" v-if="viewType" @click="viewType = false">
          <van-icon name="edit" />
          编辑
        </span>
        <span v-else @click="viewType = true"> 完成 </span>
      </div>
      <!-- 购物车列表 -->
      <div class="cart-list">
        <div class="cart-item" v-for="item in cartList" :key="item.goods_id">
          <van-checkbox
            :value="item.isCheck"
            @click="changeCheck(item.goods_id)"
          ></van-checkbox>
          <div class="show">
            <img :src="item.goods.goods_image" alt="" />
          </div>
          <div class="info">
            <span class="tit text-ellipsis-2"
              >新Pad 14英寸 12+128 远峰蓝 M6平板电脑
              智能安卓娱乐十核游戏学习二合一
              低蓝光护眼超清4K全面三星屏5GWIFI全网通 蓝魔快本平板</span
            >
            <span class="bottom">
              <div class="price">
                ¥ <span>{{ item.goods.goods_price_min }}</span>
              </div>

              <CountBox
                :value="item.goods_num"
                @input="
                  (value) =>
                    changeCartCount(item.goods_id, value, item.goods_sku_id)
                "
              ></CountBox>
            </span>
          </div>
        </div>
      </div>
      <!-- 底部 -->
      <div class="footer-fixed">
        <div class="all-check">
          <van-checkbox
            icon-size="18"
            :value="isAllCheck"
            @click="setAllCheck"
          ></van-checkbox>
          全选
        </div>
        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span
              >¥ <i class="totalPrice">{{ getSelPrice }}</i></span
            >
          </div>
          <div
            v-if="viewType"
            class="goPay"
            :class="{ disabled: getSelTotal === 0 }"
            @click="toPay"
          >
            结算({{ getSelTotal }})
          </div>
          <div
            v-else
            class="delete"
            :class="{ disabled: getSelTotal === 0 }"
            @click="clearGoods"
          >
            删除
          </div>
        </div>
      </div>
    </div>
    <!-- 空购物车 -->
    <div class="empty-cart" v-else>
      <img src="../../assets/empty.png" alt="" />
      <div class="tips">您的购物车是空的, 快去逛逛吧</div>
      <div class="btn" @click="$router.push('/')">去逛逛</div>
    </div>
  </div>
</template>

<script>
import CountBox from '@/components/CountBox.vue'
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'CartIndex',
  data () {
    return {
      viewType: true
    }
  },
  computed: {
    ...mapState('cart', ['cartList']),
    ...mapGetters('cart', [
      'getTotal',
      'getSelTotal',
      'getSelPrice',
      'isAllCheck',
      'getSelList'
    ]),
    getToken () {
      return this.$store.getters.token
    }
  },
  components: {
    CountBox
  },
  created () {
    if (this.getToken) {
      this.$store.dispatch('cart/getCartList')
    }
    if (this.cartList) {
      console.log(this.cartList)
    }
  },
  methods: {
    changeCheck (goodsId) {
      this.$store.commit('cart/changeCheck', goodsId)
    },
    setAllCheck () {
      this.$store.commit('cart/setAllCheck', this.isAllCheck)
    },
    changeCartCount (goodsId, goodsNum, goodsSkuId) {
      this.$store.dispatch('cart/changeCartCount', {
        goodsId,
        goodsNum,
        goodsSkuId
      })
    },
    async clearGoods () {
      if (this.getSelTotal === 0) return
      await this.$store.dispatch('cart/clearCartGoods')
      this.viewType = true
    },
    // 跳转订单结算页面
    toPay () {
      if (this.getSelTotal) {
        const selGoods = this.getSelList.map(item => item.id).join(',')
        this.$router.push({
          path: '/pay',
          query: {
            mode: 'cart',
            cartIds: selGoods
          }
        })
      }
    }
  },
  watch: {
    viewType (value) {
      if (value) {
        // 监视到购物车结算状态 全选
        this.$store.commit('cart/setAllCheck', false)
      } else {
        // 监视到购物车删除商品状态 取消全选
        this.$store.commit('cart/setAllCheck', true)
      }
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
  .header {
    height: 8vh;
    background-color: rgb(158, 204, 222);
    text-align: center;
    line-height: 8vh;
  }
.cart {
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }
  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;
    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}
.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }
  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }
    .goPay,
    .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }
}
.empty-cart {
  padding: 12vh 6vh;
  img {
    width: 20vh;
    height: 14vh;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 6vh auto;
  }
  .btn {
    width: 16vh;
    height: 5vh;
    line-height: 5vh;
    font-size: 2vh;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 2vh;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
</style>
